import React from 'react';

export default class Description extends React.Component
{
    render(){
        return(
            <div className="skills-box">
                <div className="green scale-font center myskills-parent">
                    <h1 className="myskills-child">My Skills</h1>
                </div>
                <div className="row">
                    {generateSkillsList()}
                </div>
            </div>
        )
    }
}

const generateSkillsList = () => {
    let skills = [
        "Javascript", "AWS", "JAVA", "REST", "DevOps", "Node.js", "Agile Methodology", "Terraform", "Release Automation", "Test Automation", "GIT", "SQL",
        "Devops", ".NET", "Docker", "Interpersonal Communication", "API", "C++", "Self-motivated", "Ansible", "React", "Python", "Software development"
    ];

    const numColumns = Math.ceil(skills.length / 8);

    let rows = [];

    for(let i = 0; i < numColumns; i++)
    {
        const cells = [];
        for(let j = 0; j < 8; j++)
        {
            let index = i * 8 + j;
            if(index >= skills.length)
            {
                break;
            }
            cells.push(<li className="list-group-item" key={index}>{skills[index]}</li>);
        }
        rows.push(<div className="col-sm-4"><ul className="list-group scale-font" key={i}>{cells}</ul></div>)
    }

    return rows;
}